<template>
  <div class="shell" id="shell">
    <div class="header">
      <h2 class="title">勿忘草的前端</h2>
      <h3 class="subtitle">BILIBILI</h3>
    </div>
    <div class="timeline">
      <div class="item" data-text="《永别，时间》">
        <div class="content">
          <img class="img" src="@/assets/bgi/ShaftImages/asuka_91487633_p1.jpg" alt="" />
          <h2 class="content-title">2002</h2>
          <p class="content-desc">时间就是这样：夜里想着心事，第二天的闹铃就响了；下几次雨，夏天也结束了；等反应过来的时候，一年已经快要过去了。</p>
        </div>
      </div>
      <div class="item" data-text="《你的孤独，虽败犹荣》">
        <div class="content">
          <img class="img" src="@/assets/bgi/ShaftImages/asuka_91487633_p1.jpg" alt="" />
          <h2 class="content-title">2002</h2>
          <p class="content-desc">如果你停止，就是谷底。如果你还在继续， 就是上坡。这是我听过关于人生低谷最好的阐述。</p>
        </div>
      </div>
      <div class="item" data-text="《你的孤独，虽败犹荣》">
        <div class="content">
          <img class="img" src="@/assets/bgi/ShaftImages/asuka_91487633_p1.jpg" alt="" />
          <h2 class="content-title">2002</h2>
          <p class="content-desc">如果你停止，就是谷底。如果你还在继续， 就是上坡。这是我听过关于人生低谷最好的阐述。</p>
        </div>
      </div>
      <div class="item" data-text="《你的孤独，虽败犹荣》">
        <div class="content">
          <img class="img" src="@/assets/bgi/ShaftImages/asuka_91487633_p1.jpg" alt="" />
          <h2 class="content-title">2002</h2>
          <p class="content-desc">如果你停止，就是谷底。如果你还在继续， 就是上坡。这是我听过关于人生低谷最好的阐述。</p>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'body',
  mixins: [],
  components: {},
  props: {},
  data() {
    return {}
  },
  methods: {
    start() {
      var shell = document.querySelector('.shell')
      var timeline = document.querySelector('.timeline')
      var items = timeline.querySelectorAll('.item')
      var activeClass = 'item--active'
      var imgSelector = '.img'

      // 将第一个时间轴项目激活，并设置时间轴背景图片为第一个项目的图片
      items[0].classList.add(activeClass)
      shell.style.backgroundImage = 'url(' + items[0].querySelector(imgSelector).src + ')'

      // 获取时间轴项目的总数
      var itemLength = items.length

      // 当页面滚动时，触发滚动事件
      window.addEventListener('scroll', function () {
        var max, min
        // 获取页面滚动的距离
        var pos = window.pageYOffset || document.documentElement.scrollTop || document.body.scrollTop || 0

        items.forEach(function (item, i) {
          // 获取当前时间轴项目的最小和最大高度
          min = item.offsetTop
          max = item.offsetHeight + item.offsetTop

          // 如果滚动到最后一个项目，并且超过了当前项目高度的一半，
          // 则将最后一个项目设置为激活状态，并设置背景图片为最后一个项目的图片
          if (i === itemLength - 2 && pos > min + item.offsetHeight / 2) {
            items.forEach(function (item) {
              item.classList.remove(activeClass)
            })
            shell.style.backgroundImage = 'url(' + items[itemLength - 1].querySelector(imgSelector).src + ')'
            items[itemLength - 1].classList.add(activeClass)
          }
          // 如果当前滚动位置在当前项目的最小和最大高度之间，
          // 则将当前项目设置为激活状态，并设置背景图片为当前项目的图片
          else if (pos <= max - 10 && pos >= min) {
            shell.style.backgroundImage = 'url(' + item.querySelector(imgSelector).src + ')'
            items.forEach(function (item) {
              item.classList.remove(activeClass)
            })
            item.classList.add(activeClass)
          }
        })
      })
    },
  },
  created() {},
  mounted() {
    this.start()
  },
  computed: {},
  watch: {},
}
</script>

<style scoped>
* {
  padding: 0;
  margin: 0;
}

.shell {
  width: 100%;
  position: relative;
  padding: 80px 0;
  transition: 0.3s ease 0s;
  background-attachment: fixed;
  background-size: cover;
}

.shell:before {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background: rgba(99, 99, 99, 0.8);
  content: '';
}

.header {
  width: 100%;
  text-align: center;
  margin-bottom: 80px;
  position: relative;
}

.title {
  color: #fff;
  font-size: 46px;
  font-weight: normal;
  margin: 0;
}

.timeline {
  display: flex;
  margin: 0 auto;
  flex-wrap: wrap;
  flex-direction: column;
  max-width: 700px;
  position: relative;
}

.content-title {
  font-weight: normal;
  font-size: 66px;
  margin: -10px 0 0 0;
  transition: 0.4s;
  padding: 0 10px;
  box-sizing: border-box;
  color: #fff;
}

.content-desc {
  margin: 0;
  font-size: 15px;
  box-sizing: border-box;
  color: rgba(255, 255, 255, 0.7);
  line-height: 25px;
}

.timeline:before {
  position: absolute;
  left: 50%;
  width: 2px;
  height: 100%;
  margin-left: -1px;
  content: '';
  background: rgba(255, 255, 255, 0.07);
}

.item {
  padding: 40px 0;
  opacity: 0.3;
  filter: blur(2px);
  transition: 0.5s;
  box-sizing: border-box;
  width: calc(50% - 40px);
  display: flex;
  position: relative;
  transform: translateY(-80px);
  cursor: pointer;
}

.item:before {
  content: attr(data-text);
  letter-spacing: 3px;
  width: 100%;
  position: absolute;
  color: rgba(255, 255, 255, 0.5);
  font-size: 13px;
  border-left: 2px solid rgba(255, 255, 255, 0.5);
  top: 70%;
  margin-top: -5px;
  padding-left: 15px;
  opacity: 0;
  right: calc(-100% - 56px);
  font: 900 20px '';
  letter-spacing: 5px;
}

.item:nth-child(even) {
  align-self: flex-end;
}

.item:nth-child(even):before {
  right: auto;
  text-align: right;
  left: calc(-100% - 56px);
  padding-left: 0;
  border-left: none;
  border-right: 2px solid rgba(255, 255, 255, 0.5);
  padding-right: 15px;
}

.item--active {
  opacity: 1;
  transform: translateY(0);
  filter: blur(0px);
}

.item--active:before {
  top: 50%;
  transition: 0.3s all 0.2s;
  opacity: 1;
}

.item--active .content-title {
  margin: -50px 0 20px 0;
}

.img {
  max-width: 100%;
  box-shadow: 0 10px 15px rgba(0, 0, 0, 0.4);
}

.subtitle {
  color: rgba(255, 255, 255, 0.5);
  font-size: 16px;
  letter-spacing: 5px;
  margin: 10px 0 0 0;
  font-weight: normal;
}

.footer {
  padding: 95px 0;
  text-align: center;
}

.footer a {
  color: #999;
  display: inline-block;
}

@media only screen and (max-width: 767px) {
  .item {
    align-self: baseline !important;
    width: 100%;
    padding: 0 30px 150px 80px;
  }

  .item:before {
    left: 10px !important;
    padding: 0 !important;
    top: 50px;
    text-align: center !important;
    width: 60px;
    border: none !important;
  }

  .item:last-child {
    padding-bottom: 40px;
  }
}

@media only screen and (max-width: 767px) {
  .timeline:before {
    left: 40px;
  }
}
</style>
